	module.exports = {"data":{"post":{"html":"<p>Interestingly, my post on <a href=\"http://www.papascott.de/archives/2004/09/27/stars/\" title=\"PapaScott - Stars\">Stars</a>  generated a number of mails from my non-virtual community, er, I mean from friends and relatives, with suggestions on where to find star stickers (with Christmas decorations) and wondering if I was really serious about reforming German education with sticky bits of glossy paper.</p>\n<p>So to clarify, the post was written with tongue in cheek. My experience with German education is zero, other than what is reported in the media, and all I really know is that children start first grade with a paper cone full of toys and candy, and come out 10 or 12 or 13 years later with an <em>Abitur</em> or an apprenticeship. I have no idea what happens in between.</p>\n<p>I use the writing in my weblog to experiment, to try new points of view, to strike poses, to distract myself from the real world. The viewpoints expressed here may have no basis in reality. I don't always tell the truth, but I never lie. The posts here are a mixture of fact, fiction and imagination, and the recipe is different every day. Enjoy the ride.</p>","fields":{"title":"Seeing Stars","date":"29 Sep 2004","url":"https://papascott.de/2004-09-29-seeing-stars/"}}},"pathContext":{"slug":"/2004-09-29-seeing-stars/","prev":{"fields":{"title":"Any Questions?","date":"29 Sep 2004","slug":"/2004-09-29-any-questions/"}},"next":{"fields":{"title":"Tony Blair Does Monty Python","date":"29 Sep 2004","slug":"/2004-09-29-tony-blair-does-monty-python/"}}}}

